.Header_Container{
    display: flex;
    text-align: center;
    justify-content: space-around;
    text-align: right;
    align-items: center;
    margin-top: 75px;
    padding: 0 50px;
    img{
        height: 600px;
    }
    .c2{
        max-width: 530px;
        display: flex;
        flex-direction: column;
        align-items: center;
        p{
            color: white;
        }
    }
    h2{
        color: white;
    }
}
// ----------------- Media Query ------------------- //

@media only screen and (max-width: 1000px) {
    .Header_Container{
        img{
            height: 400px;
        }
        
    }
 
}
@media only screen and (max-width: 860px) {
    .Header_Container{
        img{
            height: 350px;
        }
    }
 
}
@media only screen and (max-width: 650px) {
    .Header_Container{
        flex-wrap: wrap;
        text-align: center;
        img{
            height: 250px;
        }
    }
 
}
