.VirtualWorlds_Container{
    color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    p{
        max-width: 535px;
        margin: 30px 0 50px 0;
    }
    .renderedImages{
        display: flex;
        justify-content: space-between;
        width: 100%;
        cursor: pointer;
        max-width: 535px;
        .images{
            width: 90px;
            height: 90px;
        }
    }
    .mainImage{
        width: 535px;
    }
    .title{
        font-family: Meshroom;
        font-size: 2rem;
        letter-spacing: 1px;
    }
    .chosenImage{
        display: flex;
        align-items: center;
        button{
            background-color: transparent;
            border: none;
            font-size: 4rem;
            color: white;
            cursor: pointer;
            margin: 0 40px;
        }
    }
}
// ----------------- Media Query ------------------- //

@media only screen and (max-width: 860px) {
    .VirtualWorlds_Container{
        padding: 0 30px;
        p{
            max-width: 60vw;
        }
        .renderedImages{
            max-width: 60vw;
            .images{
                width: 10vw;
                height: 10vw;
            }
        }
        .mainImage{
            width: 60vw;
        }
        .chosenImage{
            button{
                margin: 0px;
            }
        }
        
    }
 
}
@media only screen and (max-width: 600px) {
    .VirtualWorlds_Container{
        padding: 0 30px;
        p{
            max-width: 90%;
        }
        .title{
            font-family: Meshroom;
            font-size: 1.6rem;
            letter-spacing: 1px;
        }
 
    }
 
}
@media only screen and (max-width: 400px) {
    .VirtualWorlds_Container{
     
        .title{
            font-size: 1.4rem;
        }
 
    }
 
}