.Subscribe_Container{
    background-image: url(../../assets/images/Sun.png);
    background-size: cover;
    background-position: bottom;
    height: 110vh;
    padding-top: 450px;
    margin-top: -350px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    .sun{
        width: 100vw;
        margin-top: -40vw;
    }
    .p{
        font-family: Meshroom;
        font-size: 18px;
    }
    .input{
        width: 560px;
        height: 50px;
        background-color: transparent;
        border: 1px solid black;
        color: rgba(0,0,0,.8);
        padding: 0 20px;
        border-radius: 5px;
    }
    input:focus{
        outline: none;
    }
    button{
        font-family: Meshroom;
        width: 605px;
        height: 50px;
        border-radius: 5px;
        background-color: #310758;
        color: white;
        border: none;
        font-size: 18px;
        margin-top: 10px;
    }
    form{
        display: flex;
        flex-direction: column;
        align-items: center;
    }
}

// ----------------- Media Query ------------------- //

@media only screen and (max-width: 700px) {
    .Subscribe_Container{
        height: 90vh;
        .sun{
            width: 100vw;
            margin-top: -40vw;
        }
     
        .input{
            width: 70vw;
            padding: 0 5vw;

        }
        input:focus{
            outline: none;
        }
        button{
            width: 81vw;
        }
        .p{
            font-family: Gotham;
            font-size: 16px;
        }
    }
}
@media only screen and (max-width: 600px) {
    .Subscribe_Container{
        .p{
            font-size: 12px;
        }
    }
}