.About_Container{
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    text-align: left;
    padding-left: 50px;
    margin: 150px 0;
    .image{
        height: 150px;
    }
    .whitepaper{
        height: 25px;
    }
    .c1{
        .h2{
            color: white;
            margin-bottom: 0.5rem;
        }
